import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { BootstrapVue3 } from 'bootstrap-vue-3'

// 전역으로 등록할 컴포넌트를 import 한다.
// import status from './components/status.vue' -> 경로에서 '.vue'는 생략 가능
import status from './components/status'
import newPage from './components/newPage'
import newHeader from './components/newHeader'
import newSection from './components/newSection'
import newArticle from './components/newArticle'
import newFooter from './components/newFooter'
import newAside from './components/newAside'
import rustHeader from './components/RustMartHeader'
import rustMart from './components/RustMart'
import rustFooter from './components/RustMartFooter'
import rustPage from './components/RustMartPage'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue-3/dist/bootstrap-vue-3.css'

// 전역으로 컴포넌트 등록
const app = createApp(App)
// app.component(사용 시 호출할 이름, import한 컴포넌트 이름)
app.component('status', status)
app.component('newPage', newPage)
app.component('newHeader', newHeader)
app.component('newSection', newSection)
app.component('newArticle', newArticle)
app.component('newFooter', newFooter)
app.component('newAside', newAside)
app.component('rustHeader', rustHeader)
app.component('rustMart', rustMart)
app.component('rustFooter', rustFooter)
app.component('rustPage', rustPage)

// app.mount('#app') <- 하나일 경우

// createApp(App).use(store).use(router).mount('#app')
app.use(store).use(router).use(BootstrapVue3).mount('#app')
