import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/new',
    name: 'new',
    component: () => import(/* webpackChunkName: "new" */ '../views/NewView.vue')
  },
  {
    path: '/rustmart',
    name: 'rustmart',
    component: () => import(/* webpackChunkName: "rustmart" webpackPrefetch:true */ '../views/RustMartView.vue')
  },
  {
    path: '/rustpage',
    name: 'rustpage',
    component: () => import(/* webpackChunkName: "rustmart" */ '../views/RustMartPageView.vue')
  },
  {
    path: '/uservue',
    name: 'uservue',
    component: () => import(/* webpackChunkName: "about" */ '../components/userVue/User.vue')
  },
  {
    path: '/uservueex',
    name: 'uservueex',
    component: () => import(/* webpackChunkName: "about" */ '../components/userVueEx/User.vue')
  },
  {
    path: '/newpage',
    name: 'newpage',
    component: () => import(/* webpackChunkName: "about" */ '../views/newPageView.vue')
  },
  {
    path: '/join',
    name: 'join',
    component: () => import(/* webpackChunkName: "join" */ '../components/join/JoinUser.vue')
  },
  {
    path: '/bootstrap',
    name: 'bootstrap',
    component: () => import(/* webpackChunkName: "bootstrap" */ '../views/BootstrapPageView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
