<template>
    <div id="wrap">
        <!-- Header -->
        <rustHeader id="header" />
        <!-- 메인 페이지 -->
        <div v-if="bVisible === true">
            <div v-for="product in proList" :key="product.productId" class="item">
                <!-- <router-link to="/rustpage"> -->
                <img class="prodImg" :src="product.imgPath" :alt="product.productName" @click="detailShow" />
                <!-- </router-link> -->
                <p>{{ product.productName }}</p>
                <p>${{ product.price }}</p>
                <button class="btnAdd" @click="addCart">Add To Cart</button>
            </div>
        </div>

        <!-- 상세페이지 -->
        <div v-if="bVisible === false">
            <div class="detailItem">
                <img class="detailImg" :src='require(`@/assets/img/`+detailImg)' :alt="detailImg" />
                <p>{{ detailName }}</p>
                <p>{{ detailPrice }}</p>
                <button class="btnAdd" @click="addCartPrice">Add To Cart</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    components: {},
    data() {
        return {
            bVisible: true,
            proList: [
                {productId: '1', productName: 'Apple', price: 3.65, imgPath: require('@/assets/img/apple.png'), context: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.'},
                {productId: '2', productName: 'Banana', price: 7.99, imgPath: require('@/assets/img/banana.png')},
                {productId: '3', productName: 'Cherries', price: 2.93, imgPath: require('@/assets/img/cherries.png')},
                {productId: '4', productName: 'Lemon', price: 1.2, imgPath: require('@/assets/img/lemon.png')},
                {productId: '5', productName: 'Mango', price: 10, imgPath: require('@/assets/img/mango.png')},
                {productId: '6', productName: 'Tangerine', price: 4.79, imgPath: require('@/assets/img/tangerine.png')},
                {productId: '7', productName: 'Watermelon', price: 2.4, imgPath: require('@/assets/img/watermelon.png')},
                {productId: '8', productName: 'Strawberry', price: 5, imgPath: require('@/assets/img/strawberry.png')},
                {productId: '9', productName: 'Avocado', price: 6.4, imgPath: require('@/assets/img/avocado.png')},
                {productId: '10', productName: 'Pineapple', price: 15, imgPath: require('@/assets/img/pineapple.png')}
            ]
        }
    },
    setup() {},
    created() {},
    mounted() {},
    unmounted() {},
    methods: {
        detailShow(event) { //상세페이지 출력
            const thisNode = event.target.parentNode

            // 자식노드의 이미지 주소를 가져옴 (ex, http://localhost:8080/img/apple.5b22332.png)
            // console.log(thisNode.childNodes[1].src)
            // 자식노드 중에 바인딩된 파일명만 잘라서 가져옴 (ex, apple.5b22332.png)
            this.detailImg = thisNode.childNodes[1].src.split('/')[4]
            // 주소에서 바인딩값은 제거하고 이미지 파일명만 잘라 붙임 (ex, apple.png)
            this.detailImg = this.detailImg.split('.')[0]+"."+this.detailImg.split('.')[2]

            this.detailName = thisNode.childNodes[3].textContent
            this.detailPrice = thisNode.childNodes[4].textContent
            this.bVisible = false
        },
        addCartPrice(event) {
            // 선택한 아이템의 가격
            let price = this.detailPrice
            const chPrice = document.querySelector('#price')

            chPrice.textContent = price.toString()
        },
        addCart(event) {
            // 버튼(add to Cart)의 상위노드
            const thisNode = event.target.parentNode

            // 아이템 선택 전 현재 총 가격 - 초기값 : 0
            let crntTotPrice = 0 // 아무것도 선택하지 않았기 때문에
            // rustHeader의 id가 price인 노드 선택
            const chPrice = document.querySelector('#price')
            // price 요소의 텍스트 콘텐츠를 가져와서 '$'를 기준으로 나눠서 배열의 첫번째 value를 대입
            crntTotPrice = chPrice.textContent.split('$')[1]

            // 선택한 아이템의 가격
            let price = thisNode.childNodes[4].textContent.split('$')[1]

            // 선택한 아이템들의 총 합계 - 초기값 : 0
            let itemTotSum = 0
            // 현재 총 가격과 선택한 아이템 가격 Add
            // -> 가격의 타입이 문자일 수 있어서 Number로 타입 변환
            itemTotSum += Number(crntTotPrice) + Number(price)
            // toFixed(2) : 소수점 2자리까지만 표시
            itemTotSum = itemTotSum.toFixed(2)

            /*
            console.log("아이템 선택 전 현재 총 가격 : " + crntTotPrice)
            console.log("선택한 아이템 가격 : " + price)
            console.log("선택한 아이템까지의 총 가격 : " + itemTotSum)
            */

            // 총합계를 #price에 출력
            chPrice.textContent = '$' + itemTotSum.toString()
        }
    }
}
</script>
<style scoped>
.prodImg {
    width: 75%;
    cursor: pointer;
}

.item {
    padding: 10px;
    background: #fff;
    /* box-shadow: 3px 3px 3px #e2e2e2 inset; */
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    border-radius: 10px;
    width: 12vw;
    float: left;
    margin: 13px;
}

button {
    background-color: rgb(203, 51, 51);
    color: white;
    padding: 15px;
    border: none;
    font-size: 15px;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}

.detailItem {
    font-weight: bold;
}

.detailImg {
    padding-top: 17rem;
    width: 17rem;
}

#header {
    cursor: pointer;
}
</style>
